<template>
  <div class="sobre">
    <img alt="" class="logo" src="../assets/logo.svg">
    <div class="columns is-mobile is-centered">
      <div class="column">
        <b-message :closable="false" size="is-large" title="Resultados e funcionamento" type="is-default">
          Hoje a rede neural  avaliada em mais de 80% de preciso, com base em 359 notcias. Isto no significa que a
          rede no pode errar. Ela tem constante dificuldade em avaliar textos recheados de opinio,  possvel que
          nestes casos a rede neural erre (seja avaliando o texto como verdadeiro ou falso). Por isto, o domnio das notcias, atualmente,  limitado em notcias de ampla repercusso nacional, falas de figuras pblicas e entre outros.
          <br>A rede neural est em constante atualizao e melhorias que podem ser acompanhadas pelo repositrio do Github.<br>
          Um diagrama sobre o funcionamento do programa pode ser encontrado abaixo.
        </b-message>
        <img alt="" class="logo" src="../assets/fig_diagram.png">
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "Duvidas",
  }
</script>

<style scoped>
  img.logo {
    padding-left: 10px;
  }

  h1.title {
    font-size: 20px;
  }
</style>



