/*
Esse cdigo foi adaptado de https://github.com/n-johnson/relative-date/blob/master/lib/relative-date.js
O propsito da adaptao consiste em traduzir as informaes das datas
 */
var relativeDate = (function(undefined){

  var SECOND = 1000,
    MINUTE = 60 * SECOND,
    HOUR = 60 * MINUTE,
    DAY = 24 * HOUR,
    WEEK = 7 * DAY,
    YEAR = DAY * 365,
    MONTH = YEAR / 12;

  var formats = [
    [ 0.7 * MINUTE, 'neste momento' ],
    [ 1.5 * MINUTE, 'um minuto atrs' ],
    [ 60 * MINUTE, 'minutos atrs', MINUTE ],
    [ 1.5 * HOUR, 'uma hora atrs' ],
    [ DAY, 'horas atrs', HOUR ],
    [ 2 * DAY, 'ontem' ],
    [ 7 * DAY, 'dias atrs', DAY ],
    [ 1.5 * WEEK, 'uma semana atrs'],
    [ MONTH, 'semanas atrs', WEEK ],
    [ 1.5 * MONTH, 'um ms atrs' ],
    [ YEAR, 'meses atrs', MONTH ],
    [ 1.5 * YEAR, 'um ano atrs' ],
    [ Number.MAX_VALUE, 'anos atrs', YEAR ]
  ];

  function relativeDate(input,reference){
    !reference && ( reference = (new Date).getTime() );
    reference instanceof Date && ( reference = reference.getTime() );
    input instanceof Date && ( input = input.getTime() );

    var delta = reference - input,
      format, i, len;

    for(i = -1, len=formats.length; ++i < len; ){
      format = formats[i];
      if(delta < format[0]){
        return format[2] == undefined ? format[1] : Math.round(delta/format[2]) + ' ' + format[1];
      }
    };
  }

  return relativeDate;

})();

if(typeof module != 'undefined' && module.exports){
  module.exports = relativeDate;
}



